import React from 'react'
import {TaskProps} from '../types/types'


function Task({tasks}: TaskProps) {
  return (
    <table className='w-full '>
        <thead className='border-b border-gray-200'>
            <tr>
                <th>Title</th>
                <th>Status</th>
                <th>Priority</th>
                <th>Due Date</th>
                <th>Description</th>
                <th>Actions</th>
            </tr>
        </thead>
        <tbody>
            {tasks.length > 0 ? (
                tasks.map((todo)=>(
                    <tr 
                        className='border-b border-gray-200'
                        key={todo.id}
                    >
                        <td className='text-center p-2'>{todo.title}</td>
                        <td className='text-center'>{todo.status}</td>
                        <td className='text-center'>{todo.priority}</td>
                        <td className='text-center'>{new Date(todo.dueDate).toDateString()}</td>
                        <td className='text-center'>{todo.description}</td>
                        <td className='flex gap-5 justify-center'>
                            <button className=' mt-1 rounded-xl bg-green-400 pt-1 pb-1 pr-4 pl-4'>Edit</button>
                            <button className=' mt-1 rounded-xl bg-red-400 pt-1 pb-1 pr-4 pl-4'>Delete</button>
                        </td>
                    </tr>
                ))
            ):(
                <tr className='w-full flex justify-center'>
                    <td>No task yet</td>
                </tr>
            )}
        </tbody>
    </table>
  )
}

export default Task